// Gatsby supports TypeScript natively!
import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import ContactForm from "../components/form"
import { Container, Row, Col, Card, Accordion } from "react-bootstrap"
import { FaChevronDown, FaChevronRight, FaFacebook, FaInstagram } from 'react-icons/fa'


let blankState = {
    'a': false,
    'b': false,
    'c': false
}

let originalState = Object.assign({}, blankState);

export default class Contact extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            questions: originalState
        };


        this.toggle = this.toggle.bind(this);
    }

    toggle(id){
        
        let currentValue = this.state.questions[id];
        let newValue = !currentValue;

        let newState = Object.assign({}, blankState);
        newState[id] = newValue;

        this.setState({questions:newState})

    }

    render(){
        return (
            <Layout>
                <SEO title="Contact Us" />
                <div className="jumbotron">
               
                    <Container>
                        <Row md={2} sm={1} xs={1}>
                            <Col md={{span: 8, order: 1}} sm={{order: 2}} xs={{order:2}}>
                                <Row>
                                    <Col>
                                        <Card>
                                            <Card.Body>
                                                <h2>Ready to book?</h2>
                                                <p>Fill out the form and we'll respond to any enquiries or booking requests as soon as possible!</p>
                                                <ContactForm/>
                                            </Card.Body>
                                        </Card>
                                        
                                        
                                    </Col>
                                </Row>
                                <Row>
                                    <Col style={{paddingTop: 32}}>
                                        <Card>
                                            <Card.Body>
                                                <h2>Frequently asked questions</h2>
                                                <span className='font-weight-light'>Click on a panel to expand it</span>
                                                <Accordion style={{marginTop: 8}}>
                                                    <Card>
                                                        <Accordion.Toggle as={Card.Header} eventKey="0" className='question-box' onClick={() => this.toggle('a')}>
                                                            <span className='question-header'> { this.state.questions['a'] ? <FaChevronDown /> : <FaChevronRight/> } Can I bring a lovely doggo to stay?</span>
                                                        </Accordion.Toggle>
                                                        <Accordion.Collapse eventKey="0">
                                                            <Card.Body>Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test Test!</Card.Body>
                                                        </Accordion.Collapse>
                                                    </Card>
                                                    <Card>
                                                        <Accordion.Toggle as={Card.Header} eventKey="1" onClick={() => this.toggle('b')}>
                                                            <span className='question-header'>{ this.state.questions['b'] ? <FaChevronDown /> : <FaChevronRight/> } What about a cats, are they allowed?</span>
                                                        </Accordion.Toggle>
                                                        <Accordion.Collapse eventKey="1">
                                                            <Card.Body>Absolutely not!</Card.Body>
                                                        </Accordion.Collapse>
                                                    </Card>
                                                    <Card>
                                                        <Accordion.Toggle as={Card.Header} eventKey="2" onClick={() => this.toggle('c')}>
                                                            <span className='question-header'>{ this.state.questions['c'] ? <FaChevronDown /> : <FaChevronRight/> } How do long questions appear in this card/accordion dropdown thing?</span>
                                                        </Accordion.Toggle>
                                                        <Accordion.Collapse eventKey="2">
                                                            <Card.Body>Absolutely not!</Card.Body>
                                                        </Accordion.Collapse>
                                                    </Card>

                                                    {/* Early check-in/early check-out? Pets? Children? Parking? Smoking/vaping? Wheelchair-access? Internet/WiFi? Animals? Accommodate... but please fill in form below. */}
                                                </Accordion>
                                            </Card.Body>

                                        </Card>
                                    </Col>
                                </Row>
                            </Col>
                            <Col md={{span: 4, order: 2}} sm={{order: 1}} xs={{order:1}} style={{marginBottom: 32}}>
                                <Card>
                                    <Card.Body>
                                        <h2>Get in touch!</h2>
                                        <p>We'd love to hear from you!</p>
                                        <p>Call us on: <Link to="tel:01234567890">01234 567 890</Link></p>
                                        <p>Send an email to: <Link to="mailto:rhylladevon@gmail.com">rhylladevon@gmail.com</Link></p>
                                        <p>
                                            <Link to='' style={{marginRight: 16}}><FaFacebook style={{fill: '#177af2', height: 48, width:48}}></FaFacebook></Link>  
                                            <Link to=''>
                                                <span className='instagram'>
                                                    <FaInstagram style={{fill: 'white', height: 36, width: 36}}></FaInstagram>
                                                </span>
                                            </Link>
                                        </p>
                                    </Card.Body>
                                </Card>
                                
                            </Col>
                        </Row>
                    </Container>
              
                </div>
            </Layout>
        );
    }
    
}